/* ======= HEROS ======= */

.cl-hero * {
    box-sizing: border-box;
}

@keyframes prompter-pulse {
    from { bottom: 25px }
    to { bottom: 15px }
}

.cl-hero {

  margin: 0 0 2rem;

  .cl-hero-proper {

  	width: 100%;
  	height: 300px;
  	overflow: hidden;
  	position: relative;
  	background: #002147;
  	z-index: 1;
  	box-sizing: border-box;

  }

  &.fullwidth,
  &.super {

    .cl-hero-proper {
      width: 100vw;
	    max-width: initial;
      position: relative;
      left: 50%;
      margin-left: -50vw;
    }

  }

  &.cl-has-accessibility-controls .cl-accessibility-controls-container {

    bottom: initial;
    top: 0;

    .cl-accessibility-controls {
      bottom: initial;
      top: 1rem;
    }

  }

  div[data-vimeo-initialized] {
    width: 100%;
    height: 100%;
  }

  iframe {
      z-index: 1;
      position: relative;
      max-width: initial;
      max-height: initial;
  }

  .still,
  .animate,
  .poster {
      z-index: 2;
      width: 100%;
      height: 100%;
      position: absolute;
      background-repeat: no-repeat;
      background-color: transparent;
      background-size: cover;
      background-position: center center;
  }

  .animate {
      transition: all 10s ease-in-out;
  }

  .poster {
      opacity: 1;
      transition: all .4s ease-in-out;
  }

  .poster.unveil {
      opacity: 0;
      transition-delay: .5s;
  }

	&.n .still {
		background-position: 50% 0;
	}
	&.ne .still {
		background-position: 100% 0;
	}
	&.e .still {
		background-position: 100% 50%;
	}
	&.se .still {
		background-position: 100% 100%;
	}
	&.s .still {
		background-position: 50% 100%;
	}
	&.sw .still {
		background-position: 0 100%;
	}
	&.w .still {
		background-position: 0 50%;
	}
	&.nw .still {
		background-position: 0 0;
	}

  .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 3;
  }

  .block {
    position: absolute;
    z-index: 3;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 2rem 5% 14%;
    text-shadow: 0 0 1rem rgba(0,0,0,.4);
  }

  &.cl-accessibility-contrast-improved {
    .block {
      text-shadow: 0 0 1rem rgba(0,0,0,.9);
      background: linear-gradient(to bottom, transparent, rgba(0,0,0,0.2) 3rem, rgba(0,0,0,0.3) 7rem);
    }
  }

  h1 {
    position: relative;
    text-align: left;
    font-weight: 700;
    font-size: 1.8rem;
    color: $white;
    transform: all .2s ease-in-out;
    padding: 0;
  }

	p {
    color: #fff;
    font-weight: 400;
    font-size: 1.1rem;
  }

	p,
	&.fullwidth p {
		max-width: 600px;
	}

  &.super {

    .cl-hero-proper {
      height: 100vh;
    }

    .block,
    h1,
    p {
      text-align: center;
    }

    h1,
    p {
      max-width: 100%;
      padding-left: 3rem;
      padding-right: 3rem;
    }

    h1 {
      font-size: 2.5rem;
    }

    p {
      font-size: 1.4rem;
      line-height: 1.5rem;
    }

  }

  .cl-button {

    border: none;
    background: $white;
    box-shadow: $level3;
    position: relative;
    top: 0;
    text-shadow: none;
    max-width: 100%;

    &:hover,
    &:focus {
      border: none;
      color: $white;
      top: -3px;
    }

  }

  .cl-hero-caption-wrapper {

    position: relative;
    max-width: 400px;
    padding-bottom: 1rem;

    &:after {
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      content: '';
      width: 50px;
      height: 1px;
      background: #ddd;
    }

  }

  .cl-hero-caption {
    font-size: 1rem;
    margin-top: 1rem;
  }

  .cl-hero-credit {
    margin-top: .5rem;
    font-size: .9rem;
    color: $darkgrey;
  }



}

@media screen and (min-width: $break1) {
  .cl-hero {

    &.cl-has-accessibility-controls .cl-accessibility-controls-container {

      top: initial;
      bottom: 0;

      .cl-accessibility-controls {
        top: initial;
        bottom: 2rem;
      }

    }

    .cl-hero-proper {
      height: 400px;
    }

    .block {
      padding-bottom: 10%;
    }

    h1 {
      font-size: 2.2rem;
    }

    p {
      font-size: 1.3rem;
    }

    &.super {

      h1 {
        font-size: 3.1rem;
      }

      p {
        font-size: 1.8rem;
        line-height: 2rem;
      }

    }

    .cl-button {
        max-width: $maxcardwidth;
    }

  }

}

@media screen and (min-width: $break2) {
  .cl-hero {

    .cl-hero-proper {
      height: 500px;
    }

    .block {
      padding-bottom: 6%;
    }

    h1 {
      font-size: 2.8rem;
    }

    p {
      font-size: 1.5rem;
    }

    &.super {

      h1 {
        font-size: 4rem;
      }

      p {
        font-size: 2rem;
        line-height: 2.2rem;
      }

    }

  }
}
